.container {
  padding-bottom: 10rem;
  height: 50rem;
  display: flex;
  justify-content: space-between;
  .text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 0 11rem;
    width: 50%;
    background-color: var(--primary);
    h2 {
      font-size: 3.2rem;
      font-weight: 600;
      margin-bottom: 1.5rem;
    }
    p {
      font-size: 1.8rem;
    }
    button {
      background-color: #000;
      border: 2px solid #000;
      color: #fff;
      font-size: 1.7rem;
      font-weight: 700;
      text-transform: uppercase;
      padding: 1rem;
      margin-top: 2rem;
      cursor: pointer;
      transition: {
        property: all;
        duration: 0.15s;
      }

      &:hover {
        background-color: initial;
        color: #000;
      }
    }
  }

  .img {
    width: 50%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
