.container {
  padding: 11rem 0;
  .heading {
    font-size: 2.6rem;
    font-weight: 600;
    margin-bottom: 4.2rem;
  }

  .grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    .item {
      cursor: pointer;
      outline: 2px solid rgba(0, 0, 0, 0.2);
      outline-offset: -2px;
      transition: {
        property: all;
        duration: 0.15s;
        timing-function: ease-in;
      }

      &:hover {
        outline: 2px solid rgba(0, 0, 0, 0.7);
      }

      .itemHeader {
        img {
          width: 100%;
        }
      }
      .itemDetails {
        padding: 1rem;
        .itemTitle {
          font-size: 1.6rem;
        }
        .itemPrice {
          font-size: 2.1rem;
          font-weight: 600;
          margin-top: 1.4rem;
        }
      }
    }
  }
}
